import { Directive, DirectiveBinding } from "vue"

export const gestureDirective: Directive = {
    beforeMount (el: any, binding: DirectiveBinding/* , vnode: VNode */) {
        el.gestureEvent = function (event: TouchEvent) {
            binding.value(event, el);
            /* if (!(el === event.target || el.contains(event.target))) {
            } */
        };
        document.body.addEventListener('touchmove', el.gestureEvent);
        document.body.addEventListener('touchstart', el.gestureEvent);
        document.body.addEventListener('touchend', el.gestureEvent);
    },
    unmounted(el: any) {
        document.body.removeEventListener('touchmove', el.gestureEvent);
        document.body.removeEventListener('touchstart', el.gestureEvent);
        document.body.removeEventListener('touchend', el.gestureEvent);
    }
}